




























































































































import { Component, Vue } from "vue-property-decorator";
import axios from "axios";
import Footer from "../components/Footer.vue";
import { AuthStore } from "@/stores/auth";

interface productInfo {
  No: Number,
  title: String,
  release: Date,
  mainAct: Array<String>,
  subAct: Array<String>,
  link: String,
  dalf: String,
  banner: String,
  hints: Array<String>
}

class productInfo implements productInfo{
  constructor(No = -1, title = "",mainAct=[""], subAct=[""], link="", dalf="", banner="", hints=[""],){
    this.No = No;
    this.title = title;
    this.mainAct = mainAct;
    this.subAct = subAct;
    this.link = link;
    this.dalf = dalf;
    this.banner = banner;
    this.hints = hints;
  }
}

@Component({
  components: { Footer },
})
export default class Products extends Vue {
  public authstore = AuthStore.getInstance();
  private selecteds = [];
  private movue = AuthStore.getInstance().movue;
  private showBannerAll = false;
  private showActor = false;
  public productDBurl: string = "https://script.google.com/macros/s/AKfycbzXYMhKrkOm3NtwqeQMBOJYc4uTz8kIbKMYOAdP0cb_lHB1BhTG8B9yc1CTyDAe_drT/exec";
  public productDB: Array<productInfo> = new Array(new productInfo());
  private headers: any = [
    { text: "", value: "No" },
    { text: "", value: "release" },
    { text: "", value: "title" },
    { text: "", value: "mainAct" },
    { text: "", value: "subAct" },
    { text: "Link", value: "link" },
  ];
  public exDB: any = "";
  public pageData: any = "";

  created() {
    //console.log(this.pageData)
    axios.get<Array<productInfo>>(this.productDBurl).then((res) => {
      this.productDB = res.data;
    });
  }
  public isBought(item:any, site:any){
    let possessionId = item.No + "-" + site;
    if (this.movue.users.possessions != undefined) return this.movue.users.possessions.includes(possessionId);
  }

  public clickStar(itemNo:any, site:any){
    const index = this.movue.users.possessions?.indexOf(itemNo)
    console.log("click possession", this.movue.users.possessions)
    if (index != -1){
      if (index != undefined) this.authstore.removePossessionStar(index);
    }else{
      this.authstore.setPossessionStar(itemNo + "-" + site);
      console.log("setPossession", itemNo + "-" + site)
    }
  }

  public search = "";
  public filter(items: any, search: string) {
    if (items == null || typeof items === "boolean") return false;
    let words = search
      .split(/,|/)
      .map((w: string) => {
        return w.trim();
      })
      .filter((x) => x);
    return (
      words.filter((w) => {
        if (Array.isArray(items)) {
          return items.filter((x: string) => x.indexOf(w) !== -1).length > 0;
        }else if(typeof items == "number"){
          return items.toString().indexOf(w) !== -1;
        }else {
          return items.toString().indexOf(w) !== -1;
        }
      }).length == words.length
    );
  }
  private c2a: any = {
      "":"",
      "":"",
      "":"",
      "":"",
      "	":"",
      "":"",
      "":"",
      "":"",
      "":"",
      "":"",
    }
  public toCharactor(actors: Array<string>){
    return actors.map(a => this.c2a[a] )
  }
  public getBoothLink(Title: string): string {
    let link: string = `https://booth.pm/ja/search/${Title}`;
    return link;
  }


  public signIn(){
    this.authstore.signin();
  }
  public signOut(){
    this.authstore.signout();
  }

  /* */
  public snack = false;
  public save() {
    console.log("save is ok");
  }
  public cancel() {
    console.log("cancel is ok");
  }
  public open() {
    console.log("open is ok");
  }
  public close() {
    console.log("close is ok");
  }
}
